<template>
    <div class="table-container">
        <div class="table-card">
            <div class="table-header">
                <h2>碳酸锂各工艺成本日度变化</h2>
                <!-- <div class="table-divider"></div> -->
            </div>
            <div class="table-wrapper">
                <table>
                    <thead>
                        <tr>
                            <th>工艺路径</th>
                            <th>{{ formatDate(currentDate) }}理论成本价</th>
                            <th>{{ formatDate(prevDate) }}理论成本价</th>
                            <th>当日涨幅（%）</th>
                            <th>五日涨幅（%）</th>
                            <th>当期利润（元/吨）</th>
                            
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in tableData" :key="index">
                            <td>{{ getProcessName(item.name) }}</td>
                            <td>{{ formatValue_floor(item.validPrice) }}</td>
                            <td>{{ formatValue_floor(item.lastvalidPrice) }}</td>
                            <td :class="getChangeClass(item.dpr)">{{ formatChange(item.dpr) }}</td>
                            <td :class="getChangeClass(item.fiveDPR)">{{ formatChange(item.fiveDPR) }}</td>
                            <td :class="getChangeClass(item.cp)">{{ formatValue_floor(item.cp) }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import axios from 'axios';

const currentDate = ref('');
const prevDate = ref('');
const tableData = ref([]);

const indicators = [
    'compoIndex',
    'spodumene',
    'lg_Lepidolite',
    'hg_Lepidolite',
    'lfp_Cathode',
    'lfp_BattPower',
    'NCM',
    'ECSPot',
    
];

const getProcessName = (name) => {
    const nameMap = {
        'compoIndex': '通惠碳酸锂综合成本指数',
        'spodumene': '锂辉石6%',
        'lg_Lepidolite': '低品位锂云母',
        'hg_Lepidolite': '高品位锂云母',
        'lfp_Cathode': '磷酸铁锂正极片',
        'lfp_BattPower': '磷酸铁锂电池粉',
        'NCM': '三元极片粉',
        'ECSPot': '电碳现货价',
        
    };
    return nameMap[name] || name;
};

const formatDate = (dateStr) => {
    if (!dateStr) return '';
    const date = new Date(dateStr);
    return `${date.getMonth() + 1}月${date.getDate()}日`;
};

// 保留两位小数
// const formatValue = (value) => {
//     if (value === null || value === undefined) return '-';
//     return Number(value).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',');
// };

// 向下取整
const formatValue_floor = (value) => {
    if (value === null || value === undefined) return '-';
    return Math.floor(Number(value) * 100) / 100;
};




const formatChange = (value) => {
    if (value === null || value === undefined) return '-';
    return `${value}%`;
};

const getChangeClass = (value) => {
    if (value === null || value === undefined) return '';
    const numValue = parseFloat(value);
    if (numValue > 0) return 'negative';
    if (numValue < 0) return 'positive';
    return '';
};

const fetchData = async () => {
    try {
        const promises = indicators.map(indicator =>
            axios.get('https://service.thqh.com.cn:27878/api/table', {
                params: { indicator }
            })
            // axios.get('http://localhost:8081/api/table', {
            //     params: { indicator }
            // })
        );

        const responses = await Promise.all(promises);
        tableData.value = responses.map(response => response.data);

        if (tableData.value.length > 0) {
            currentDate.value = tableData.value[0].currentdate;
            prevDate.value = tableData.value[0].prevdate;
        }
    } catch (error) {
        console.error('Error fetching data:', error);
    }
};

onMounted(() => {
    fetchData();
});
</script>

<style scoped>
.table-container {
    padding: 20px;

}

.table-card {
    background: rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08);
    overflow: hidden;
}

.table-header {
    padding: 20px 24px;
    background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
}

.table-header h2 {
    margin: 0;
    color: #2c3e50;
    font-size: 30px;
    font-weight: 600;
    position: relative;
    display: inline-block;
    border-radius: 16px;
    width: 420px;
    padding: 15px 30px;
    background-image: url('@/assets/小标题背景.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1;
}


.table-divider {
    height: 2px;
    margin-top: 16px;
    background: linear-gradient(90deg,
            #4ba6f5 0%,
            #33cc99 20%,
            #fdca30 40%,
            #f79400 60%,
            #f34235 80%,
            #a560e8 100%);
    border-radius: 2px;
}

.table-wrapper {
    padding: 20px;
    overflow-x: auto;
}

table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    margin-top: 10px;
}

th,
td {
    padding: 16px;
    text-align: center;
    border-bottom: 1px solid #e9ecef;
}

th {
    background: #dadada;
    color: #222222;
    font-weight: 600;
    font-size: 21px;
    white-space: nowrap;
}

td {
    color: #2c3e50;
    font-size: 18px;
}

/* 第一个指标（通惠碳酸锂综合成本指数）的特殊样式 */
tr:first-child td {
    font-size: 22px;
    font-weight: 600;
    color: #2c3e50;
    text-shadow: 
        1px 1px 0 rgba(0, 0, 0, 0.1),
        2px 2px 4px rgba(0, 0, 0, 0.1),
        0 0 10px rgba(52, 152, 219, 0.2);
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(248, 249, 250, 0.1));
}

/* 保持原有的涨跌颜色样式 */
.positive {
    color: #33cc99;
    font-weight: 500;
}

.negative {
    color: #f34235;
    font-weight: 500;
}

/* 第一个指标的涨跌颜色样式覆盖 */
tr:first-child td.positive {
    color: #33cc99;
    text-shadow: 
        1px 1px 0 rgba(0, 0, 0, 0.1),
        2px 2px 4px rgba(0, 0, 0, 0.1),
        0 0 10px rgba(51, 204, 153, 0.2);
}

tr:first-child td.negative {
    color: #f34235;
    text-shadow: 
        1px 1px 0 rgba(0, 0, 0, 0.1),
        2px 2px 4px rgba(0, 0, 0, 0.1),
        0 0 10px rgba(243, 66, 53, 0.2);
}

/* 响应式布局调整 */
@media (max-width: 768px) {
    .table-container {
        padding: 10px;
    }

    .table-wrapper {
        padding: 10px;
    }

    th,
    td {
        padding: 12px 8px;
        font-size: 13px;
    }
}

@media (max-width: 480px) {
    .table-header h2 {
        font-size: 18px;
    }

    th,
    td {
        padding: 10px 6px;
        font-size: 12px;
    }
}
</style>